import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { requiredFileType } from '../file-upload/file-upload.component';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.page.html',
  styleUrls: ['./add-item.page.scss'],
})
export class AddItemPage {

  form:any

  constructor(
    private fb: FormBuilder,
    private firestore: AngularFirestore,
    private afStorage: AngularFireStorage,
    private toastController: ToastController,
  ) {
    this.form = this.fb.group({
      name: [null, Validators.required],
      price: [null, [Validators.pattern('^[0-9]+$'), Validators.required]],
      description: [null, Validators.required],
      image: [null, [Validators.required, requiredFileType({ png: true, jpg: true, webp: true })]]

    })
    
  }


  get name() {
    return this.form.get('name')
  }
  get price() {
    return this.form.get('price')
  }
  get description() {
    return this.form.get('description')
  }
  get image() {
    return this.form.get('image')
  }


  submit() {
    let id = Math.random().toString(36).substring(2)
    let data = {
      name: this.name.value,
      id: id,
      description: this.description.value,
      price: +this.price.value,
      img: id,
    };
    this.uniqueName(this.name.value).then(e => {
      if (e.exists) {
        alert('Name is exist') 
      } else {
        this.firestore.collection('products').doc(this.name.value)
        .set(data).then(e => {
          this.afStorage.upload(`/images/${id}`, this.image.value).catch(e=> alert('error load image' + e))
          this.presentToast(`${data.name} added`)
          this.form.reset()
        }).catch(e=> alert('error ' + e))
      }
     
    })
  }

  async presentToast(message) {
    const toast = await this.toastController.create({
      message: message,
      position: 'top',
      duration: 2000,
      color: 'dark'
    });
    toast.present();
  }




  uniqueName(name): Promise<any> {
    return this.firestore.collection('products').ref.doc(name).get()
  }


}
